#cerrar_lateral, #menu_l{
    display:none;
}



#menu_lateral{
    position: fixed; 
    width:250px; 
    display:inline-block;  
    top:3%;  
    color:#fff; 
    z-index:5000;
}
.cristal{
    width:100%;
    background-color:#2f2f2f;
    opacity:.25;
    height:100%;
    position:absolute;
    top:0px;
}

#menu_principal_over{
    position:absolute; 
    top:0px; 
    width:210%; 
    background-color:#2f2f2f; 
    z-index:14;

}

@media only screen and (min-width: 768px) {
    #menu_lateral{
        top:20%;  
    } 
    #menu_principal_over{
        width:100%; 

    }
}
